import {useEffect, useRef} from "react";
import LineInGameContainer from "./components/LineInGameContainer";
import s from './App.module.css';
import React from 'react'


const App = (props) => {
    const canvasRef = useRef(null);
    const msgBoxRef = useRef(null);
    useEffect(() => {


//   ,      
            const sensitivity = 20;

// ,     
            const msgBox = msgBoxRef.current;

            let touchStart = null; //  
            let touchPosition = null; // 


//             document

            // 
            document.addEventListener("touchstart", function (e) {
                TouchStart(e);
            }); // 
            document.addEventListener("touchmove", function (e) {
                TouchMove(e);
            }); //   
//  
            document.addEventListener("touchend", function (e) {
                TouchEnd(e, "green");
            });


            function TouchStart(e) {
                //   
                // console.log('touchstart')
                touchStart = {x: e.changedTouches[0].clientX, y: e.changedTouches[0].clientY};
                touchPosition = {x: touchStart.x, y: touchStart.y};
            }

            function TouchMove(e) {
                //  
                // console.log('touchmove')
                e.preventDefault()
                touchPosition = {x: e.changedTouches[0].clientX, y: e.changedTouches[0].clientY};
            }

            function TouchEnd(e, color) {

                // console.log('touchend')
                CheckAction(); //,    

                // 
                touchStart = null;
                touchPosition = null;
            }

            function CheckAction() {
                // console.log('checkAction')
                let d = ()=> {//         
                    if (touchStart && touchPosition) {
                       return {
                           x: touchStart.x - touchPosition.x,
                           y:touchStart.y - touchPosition.y
                       }
                    }else{
                        return {
                            x:0, y: 0
                        }
                    }
                }
                let msg = ""; //

                if (Math.abs(d().x) > Math.abs(d().y)) //,      
                {
                    if (Math.abs(d().x) > sensitivity) //,     
                    {
                        if (d().x > 0) //   ,      
                        {
                            msg = "Swipe Left";
                            props.left()
                        } else //     
                        {
                            msg = "Swipe Right";
                            props.right()
                        }
                    }
                } else //    
                {
                    if (Math.abs(d().y) > sensitivity) {
                        if (d().y > 0) // 
                        {
                            msg = "Swipe up";
                            props.up()
                        } else // 
                        {
                            msg = "Swipe down";
                            props.down()
                        }
                    }
                }

                msgBox.innerText = msg; // 

            }

            let ret = () => {
                document.removeEventListener("touchstart", function (e) {
                    TouchStart(e);
                });
                // 
                document.removeEventListener("touchmove", function (e) {
                    TouchMove(e);
                });
                //   
                //  
                document.removeEventListener("touchend", function (e) {
                    TouchEnd(e);
                });
            }
            return ret()
        }
    )
    return (<div>
        <canvas ref={canvasRef} className={s.canvas} style={{overflowY: 'hidden' }}></canvas>
        <LineInGameContainer/>
        <span ref={msgBoxRef}></span>
    </div>);
}
export default App;
